@import "../common/styles/sass/variables";
@import '../common/styles/sass/mixins';

.head {
    border: 1px solid black;
    min-height: 80vh;
    background-color: $bgColor;
    display: flex;
    justify-content: center;
    text-align: center;
    width: 100%;
    .main {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-height: 60vh;
        border: 1px solid;
        width: 80%;
        .menu {
            //border: 1px solid black;
            display: flex;
            width: 230px;
            flex-direction: row;
            justify-content: space-between;
            .icon {
                background-repeat: repeat-x;
                height:40px;
                width: 40px;
                background-color: #fa0202;
            }
        }
        .description {
            @include text($TextFontFamily, 12px, 1.8);
            font-weight: 400;
        }
    }
}


