@import "../../common/styles/sass/variables";
@import "../../common/styles/sass/mixins";


.project {
  width: 450px;
  background-color: $bgSecondColor;
  display: flex;
  flex-direction: column;

  .icon {
    height: 200px;
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;

    &::before {
      position: absolute;
      content: '';
      background-color: black;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      opacity: 0.4;
    }

    .button {
      border-radius: 45%;
      padding: 10px 20px;
      background-color: $primaryColor;
      position: relative;
      z-index: 1;
      opacity: 0.3;
      color: $textColor;
    }
  }

  .secondMain {
    padding: 25px 20px;

    .projectTitle {
      @include text($TitleFontFamily, 16px, 1.2);
      letter-spacing: 1px;
      margin: 0 0 5px;
    }

    .description {
      @include text($TextFontFamily, 12px, 1.8);
      font-weight: 400;
    }
  }

}










