@import "../common/styles/sass/variables";
@import '../common/styles/sass/mixins';

.head {
    border: 1px solid black;
    min-height: 80vh;
    background-color: $bgColor;
    display: flex;
    justify-content: center;
    text-align: center;
    width: 100%;
    .main {
        align-items: center;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        min-height: 60vh;
        border: 1px solid;
        width: 80%;
        .description {
            @include text($TextFontFamily, 12px, 1.8);
            font-weight: 400;
        }
        .form {
            display: flex;
            flex-direction: column;
            align-items: center;
            width: 80%;
            border: 1px solid black;
            .button {
                border-radius: 45%;
                padding: 10px 20px;
                background-color: $primaryColor;
                position: relative;
                z-index: 1;
                opacity: 0.3;
                color: $textColor;
            }

            .input {
                margin: 5px;
                width: 80%;
            }
            .inputSend {
                margin: 5px;
                width: 80%;
            }
            .text {
                margin: 5px;
                min-height: 10vh;
                width: 80%;
            }


        }
    }
}



